/*Параметры контейнера */
.countdownHolder{
	width:260px;
	margin: 0 auto;
	font: 26px/1.2 Roboto, sans-serif;
	text-align:center;
	letter-spacing:-3px;
	font-weight: 700;
	.position{
		display: inline-block;
		height: 1.7em;
		overflow: hidden;
		position: relative;
		width: 0.55em;
	}
	/*Параметры цифр */
	.digit{
		position:absolute;
		display:block;
		width:0.5em;
		border-radius:0.2em;
		text-align:center;
		color:#000;
		letter-spacing:-1px;
		&.static {
			margin-top:18px;
		}
	}
	/*Параметры подложки для секций */
	.countDays,
	.countHours,
	.countMinutes,
	.countSeconds {
		background:#fff;
		padding:10px 10px;
		margin:5px;
		border-radius: 3px;
		border: 1px solid #c4c4c4;
		background-color: rgba(#dcdcdc, 0.5);
	}
	.wrapDays{
		display: none !important;
	}
	/*Параметры секций разделения (:)*/
	.countDiv {
		display:none;
		width:16px;
		height:1.6em;
		position:relative;
		&:before,
		&:after {
			position:absolute;
			width:5px;
			height:5px;
			background-color:#444;
			border-radius:50%;
			left:50%;
			margin-left:-3px;
			top:0.5em;
			box-shadow:1px 1px 1px rgba(4, 4, 4, 0.5);
			content:'';
		}
		&:after {
			top:0.9em;
		}
	}
	/*Параметры вспомогательного текста (дни, часы, минуты, секунды)*/
	.textdigit{
		font-size:15px;
		color: #7a7a7a;
		font-family: Roboto;
		font-size: 12px;
		font-weight: 400;
		line-height: 26px;
		letter-spacing: 0;
		margin-top: 10px;
	}
}
